// ÐŸÐµÑ€ÐµÐ²Ð¸ÐºÐ¾Ñ€Ð¸ÑÑ‚Ð¾Ð²ÑƒÑ”Ð¼Ð¸Ð¹ ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½Ñ‚, Ñ‰Ð¾ Ñ€ÐµÐ½Ð´ÐµÑ€Ð¸Ñ‚ÑŒ Ñƒ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð¼Ñƒ Ð²Ñ–ÐºÐ½Ñ– Ð¿Ð¸Ñ‚Ð°Ð½Ð½Ñ, Ð¾Ñ‚Ñ€Ð¸Ð¼Ð°Ð½Ðµ Ñƒ Ð²Ð¸Ð³Ð»ÑÐ´Ñ– childrens,
// Ñ‚Ð° 2 Ñ„ÑƒÐ½ÐºÑ†Ñ–Ð¾Ð½Ð°Ð»ÑŒÐ½Ñ– ÐºÐ½Ð¾Ð¿ÐºÐ¸. ÐœÐ¾Ð´Ð°Ð»ÑŒÐ½Ðµ Ð²Ñ–ÐºÐ½Ð¾ Ð¿Ð¾Ð²Ð¸Ð½Ð½Ð¾ Ð·Ð°ÐºÑ€Ð¸Ð²Ð°Ñ‚Ð¸ÑÑŒ Ð¿Ð¾ ÐºÐ»Ñ–ÐºÑƒ Ð½Ð° ÐºÐ½Ð¾Ð¿ÐºÑƒ Cancel,
// Ð¿Ð¾ ÐºÐ»Ñ–ÐºÑƒ Ð½Ð° ÐºÐ½Ð¾Ð¿ÐºÑƒ Ð·Ð°ÐºÑ€Ð¸Ñ‚Ñ‚Ñ, ÐºÐ»Ñ–Ðº Ð¿Ð¾ Ð±ÐµÐºÐ´Ñ€Ð¾Ð¿Ñƒ, Ð° Ñ‚Ð°ÐºÐ¾Ð¶ ÐºÐ»Ñ–Ðº Ð¿Ð¾ Escape.
// ÐŸÐ¾ ÐºÐ»Ñ–ÐºÑƒ Ð½Ð° Yes Ð½ÐµÐ¾Ð±Ñ…Ñ–Ð´Ð½Ð¾ Ð²Ð¸ÐºÐ»Ð¸ÐºÐ°Ñ‚Ð¸ Ñ„ÑƒÐ½ÐºÑ†Ñ–ÑŽ, Ð¿ÐµÑ€ÐµÐ´Ð°Ð½Ñƒ Ð² ModalApproveAction Ñƒ Ð²Ð¸Ð³Ð»ÑÐ´Ñ– props Ð½Ð° Ð²Ð¸Ð¿Ð°Ð´Ð¾Ðº Ð·Ð³Ð¾Ð´Ð¸ ÐºÐ¾Ñ€Ð¸ÑÑ‚ÑƒÐ²Ð°Ñ‡Ð°

import PropTypes from 'prop-types';
import { useEffect } from 'react';
import { Button, CloseIcon, Content, Modal, Overlay } from './ModalApproveAction.styled';
import icons from 'images/icons.svg';

export const ModalApproveAction = ({ children, onClose}) => {
  //   Ð¿Ñ€Ð¾Ð¿Ñ onClose Ñ†Ðµ Ñ„ÑƒÐ½ÐºÑ†Ñ–Ñ toggleModal,
  //   ÑÐºÑƒ Ð¿Ð¾Ñ‚Ñ€Ñ–Ð±Ð½Ð¾ Ð¿ÐµÑ€ÐµÐ´Ð°Ñ‚Ð¸ ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½Ñ‚Ñƒ ModalApproveAction
  //   toggleModal - Ð·Ð¼Ñ–Ð½ÑŽÑ” ÑÑ‚ÐµÐ¹Ñ‚ isOpen

  const onBackdropClose = event => {
    if (event.target === event.currentTarget) {
      onClose();
    }
  };

  useEffect(() => {
    const onEscapeClose = event => {
      if (event.code === 'Escape') {
        onClose();
      }
    };

    window.addEventListener('keydown', onEscapeClose);
    document.body.style.overflow= "hidden";

    return () => {
      document.body.style.overflow = "visible";
      window.removeEventListener('keydown', onEscapeClose);
    };
  }, [onClose]);

  return (
    <>
      <Overlay onClick={onBackdropClose}>
        <Modal >
          <Button type="button" apia-label="close" onClick={onClose}>
            <CloseIcon>
              <use href={icons + '#cross-small'} />
            </CloseIcon>
          </Button>
          <Content>{children}</Content>
        </Modal>
      </Overlay>
    </>
  );
};

ModalApproveAction.propTypes = {
  onClose: PropTypes.func.isRequired,
  children: PropTypes.node, 
};
